<style lang="scss" rel="stylesheet/scss" scoped>
  .content {
    margin-top: 140px;
  }

  .hotkey {
   display: flex;
   align-items: center;
  }

  .record {
    padding:20px;
    background: none;
    svg {
      cursor: pointer;
    }
    outline: none;
    align-self: center;
    border: none;
    border-radius: 100%;
    position: relative;
    color: white;

  }

  .key {
    font-size: 36px;
    font-weight: 900;
  }

  .badget {
    align-self: center;
    color: white;
    font-size: 12px;
    padding: 5px 10px;
    margin-left: 15px;
    border-radius: 4px;
    font-weight: 700;
    &.is-unsafe {
      background: #f54034;
    }
    &.is-safe {
      background: #49b050;
    }
  }
</style>

<template>
  <div class="content">
    <div class="container">

      <div class="hotkey">
        <button class="record">
          <svg @click="startRecording" v-if="!isRecording" fill="#f54034" height="24" viewBox="0 0 24 24" width="24"
               xmlns="http://www.w3.org/2000/svg">
            <path d="M0 0h24v24H0z" fill="none"/>
            <path
              d="M10 16.5l6-4.5-6-4.5v9zM12 2C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8z"/>
          </svg>


          <svg @click="stopRecording" v-if="isRecording" fill="#f54034" height="24" viewBox="0 0 24 24" width="24"
               xmlns="http://www.w3.org/2000/svg">
            <path d="M0 0h24v24H0z" fill="none"/>
            <path
              d="M9 16h2V8H9v8zm3-14C6.48 2 2 6.48 2 12s4.48 10 10 10 10-4.48 10-10S17.52 2 12 2zm0 18c-4.41 0-8-3.59-8-8s3.59-8 8-8 8 3.59 8 8-3.59 8-8 8zm1-4h2V8h-2v8z"/>
          </svg>

        </button>

<div v-if="keys.length === 0 && !isRecording" class="info">
  Press Play to beginn
</div>
        <div class="" style="display:flex">
        <Key v-for="key in keys" :remove="removeKey(key)" :index="$index" :key="key"></Key>
        </div>
        <span class="badget is-safe">SAFE</span>
      </div>


    </div>
  </div>
</template>

<script type="text/babel">

  import Key from '../components/Key'

  import getKeyByCode from '../services/hotkeys'

  export default {
    name: 'HotKey',

    components: {
      Key
    },
    data() {
      return {
        keys: [],
        isRecording: false
      }
    },

    ready() {
      document.onkeydown = (e) => {
        if (this.isRecording) {
          e.preventDefault()
          if(this.keys.length < 3) {

            var keyName = getKeyByCode(e.keyCode);

          this.keys.push({
            code: e.keyCode,
            name: keyName
          });
          }
        }
      }

    },
    methods: {
      startRecording() {
        this.isRecording = true;

      },
      stopRecording() {
        this.isRecording = false;
      },

      removeKey(key) {
        return () => {
          this.keys.$remove(key);
        }
      }
    },

  }

</script>




